/* Reset and base styles  */
* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Links */
a,
a:link,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/* Common */
aside,
footer,
section,
main {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-size: inherit;
  font-weight: inherit;
}

ul,
ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

/* Form */
input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button {
  display: inline-block;
  box-shadow: none;
  cursor: pointer;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

.btn {
  border-radius: 10px;
  padding: 15px 25px;
  transition: 0.2s;
}
.btn--login {
  background-color: #f4f4f4;
  border-radius: 10px;
  padding: 12px 20px;
  margin-right: 10px;
}
.btn--login:hover {
  background: #565656;
  color: #fff;
}
.btn--get {
  padding: 11px 17px;
  border: #000 solid 2px;
  border-radius: 10px;
  transition: transform 0.2s;
}
.btn--get:hover {
  background: #000;
  color: #fff;
}
.btn--download {
  background-color: #000;
  color: #fff;
}
.btn--download:hover {
  background: linear-gradient(35deg, rgba(243, 59, 124, 0.7), rgba(180, 97, 235, 0.9), rgba(62, 50, 228, 0.7));
}
.btn--contact {
  border: #000 solid 2px;
  background: #fff;
}
.btn--contact i {
  margin-left: 15px;
}
.btn--contact:hover {
  background: linear-gradient(35deg, #f8fdff, #27c1ff, #5559ff);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

body {
  font-family: "Nunito Sans", sans-serif;
  background-color: #e8e8e8;
}

.container {
  min-width: 375px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 25px 60px 25px 65px;
  background-color: #fff;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: block;
  margin-right: 15px;
}
.logo__contact {
  display: block;
  font-size: 12px;
}

.logo-pic {
  position: relative;
  width: 26px;
  height: 26px;
  background-color: #000;
  border-radius: 50%;
}
.logo-pic:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 13px;
  height: 26px;
  background-color: #fff;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav__item {
  margin-left: 15px;
  margin-right: 15px;
  list-style-type: disc;
}
.nav__item:first-child {
  list-style-type: none;
}
.nav__item:last-child {
  margin-right: 0;
}

/* section card style */
.card__container {
  display: flex;
  padding-left: 30px;
}
.card__description {
  width: 50%;
  padding-right: 100px;
}
.card__buttons {
  display: flex;
  margin-bottom: 60px;
}
.card__btn-download {
  margin-right: 18px;
}
.card__pictures {
  position: relative;
  width: 50%;
  background: linear-gradient(79deg, rgba(243, 59, 124, 0.7), rgba(180, 97, 235, 0.9), rgba(62, 50, 228, 0.7) 70%), linear-gradient(309deg, rgb(212, 99, 82), rgba(200, 141, 158, 0.7), rgba(238, 224, 22, 0.9) 70%);
  display: flex;
  justify-content: center;
  align-items: flex-end;
  overflow: hidden;
}
.card__card-user {
  position: absolute;
  bottom: 80px;
  z-index: 3;
}

.title {
  display: flex;
  margin-bottom: 40px;
}

.title__icon {
  border: #000 solid 1px;
  padding: 4px;
  border-radius: 5px;
  width: 30x;
  height: 30px;
  margin-right: 10px;
}

.title__desc {
  display: flex;
  align-items: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
}

.info__title {
  display: block;
  font-size: 50px;
  font-family: "Lexend";
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  margin-bottom: 40px;
}

.info__text {
  display: block;
  font-size: 18px;
  font-style: normal;
  color: #494949;
  margin-bottom: 40px;
}

/* buttons */
/*stats*/
.stats {
  display: flex;
}

.stats__item {
  width: 50%;
}

.stats__title {
  font-size: 50px;
  font-family: "Lexend";
  font-style: normal;
  font-weight: 700;
  line-height: 140%;
  margin-bottom: 15px;
}

.stats__info {
  font-size: 14px;
  margin-bottom: 20px;
  color: #494949;
  position: relative;
}

.stats__info--blue::after {
  content: "";
  display: block;
  width: 127px;
  height: 3px;
  position: absolute;
  background: linear-gradient(35deg, #f8fdff, #27c1ff, #5559ff);
  border-radius: 2px;
  margin-top: 20px;
}

.stats__info--pink::after {
  content: "";
  display: block;
  width: 127px;
  height: 3px;
  position: absolute;
  background: linear-gradient(35deg, #fefafb, #eb9ab6, #d73f73);
  border-radius: 2px;
  margin-top: 20px;
}

