/*Picture*/
.message-up {
  position: absolute;
  top: 65px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 90%;
  padding: 17px 15px;
  background-color: #fff;
  border-radius: 15px;
  z-index: 8;
}
.message-up__logo {
  display: block;
  width: 25px;
  height: 24px;
  margin-right: 10px;
}
.message-up__info {
  font-size: 14px;
  margin-right: 10px;
}
.message-up__sum {
  font-weight: 600;
}

.right-arrow {
  width: 15px;
  height: 12px;
  margin-left: 10px;
}

.message-down {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 206px;
  padding: 11px 20px;
  background-color: #fff;
  border-radius: 15px;
}
.message-down__logo {
  display: block;
  width: 19px;
  height: 19px;
  margin-right: 9px;
}
.message-down__sum {
  font-weight: 600;
  margin-right: 95px;
}

.phone {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 241px;
  height: 340px;
  background: linear-gradient(to bottom, #fff 74%, transparent);
  border-radius: 20px 20px 0px 0px;
  padding: 25px 20px;
}
.phone__title {
  font-size: 16px;
  font-weight: 600;
}

.phone__arrow {
  display: block;
  margin-bottom: 20px;
}

.card-user {
  max-width: 300px;
  height: 166px;
  background: #fff;
  border-radius: 10px;
  padding: 2px;
}

/* Up part of card */
.card-user__top {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 60%;
  background: #000;
  border-radius: 10px 10px 0 0;
  padding: 20px 76px 20px 22px;
  z-index: 4;
}

.card-user__logo-wrapper {
  width: 45px;
  height: 25px;
}

.card-user__logo {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-user__form {
  display: flex;
  align-items: end;
}

.card-user__field {
  width: 200px;
  font-size: 16px;
  color: #fff;
  background-color: transparent;
  border-radius: 10px;
  padding: 5px;
}

.card-user__field::placeholder {
  font-size: 14px;
  color: rgba(211, 208, 208, 0.7450980392);
}

/* Down part of card */
.card-user__bottom {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40%;
  background: #fff;
  border-radius: 0 0 10px 10px;
  padding: 20px 25px;
}

.card-items {
  width: 50%;
}
.card-items__title {
  display: block;
  font-size: 14px;
  font-weight: 700;
}
.card-items__info {
  display: block;
  font-size: 14px;
}

.circles {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: end;
}
.circles__circle-black {
  margin-right: -9px;
}

.circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}
.circle--black {
  background-color: #000;
}
.circle--white {
  border: 1px solid #000;
}

/* footer style */
.footer__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 65px 60px 25px 90px;
  background-color: #fff;
}
.footer__title {
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 45px;
  position: relative;
}
.footer__title::after {
  content: "";
  display: block;
  position: absolute;
  background-color: #ececec;
  height: 2px;
  width: 889px;
  top: 10px;
  left: 151px;
}
.footer__info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding-right: 30px;
}
.footer__item {
  width: 100px;
}

/* Media */
@media (max-width: 1024px) {
  .container {
    padding: 25px 35px 25px 40px;
  }
  .card__buttons {
    flex-direction: column;
  }
  .card__btn-download {
    margin-right: 0;
    margin-bottom: 10px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 25px 10px;
  }
  .header__container {
    flex-direction: column;
  }
  .header__logo {
    margin-bottom: 20px;
  }
  .header__nav {
    margin-bottom: 20px;
  }
  .card__container {
    padding-left: 0;
  }
  .card__description {
    padding-right: 15px;
  }
}
@media (max-width: 675px) {
  .card__container {
    flex-direction: column;
    position: relative;
  }
  .card__description {
    width: 100%;
    padding-right: 0;
    margin-bottom: 40px;
  }
  .card__pictures {
    width: 100%;
    padding-top: 20px;
  }
  .card__buttons {
    max-width: 400px;
    margin: 0 auto;
  }
  .message-up {
    display: none;
  }
  .title {
    justify-content: center;
  }
  .info__title {
    text-align: center;
  }
  .info__text {
    text-align: center;
  }
  .stats {
    margin-top: 20px;
  }
  .stats__title {
    text-align: center;
  }
  .stats__info {
    display: flex;
    justify-content: center;
  }
  .footer__info {
    flex-direction: column;
  }
  .footer__item {
    width: 200px;
    margin-bottom: 40px;
  }
}
